.event-container {
    position: relative;
    cursor: pointer;
    width: 160px;
    height: 120px;
}

.event-image-container {
    width: 160px;
    height: 120px;
    /* transform: scale(0.1); */
    object-fit: cover;
    filter: brightness(55%);
    border-radius: 10px;
    /* border: 1px solid red; */
}

.event-destination {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 10px 10px 10px 10px;
    font-size: 12pt;
    font-weight: 700;
    color: var(--light-text-color);
}

.event-info {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 0;
    padding: 10px 10px 10px 10px;
    font-size: 10pt;
    font-weight: 400;
    color: var(--light-text-color);
}

.selected {
    filter: hue-rotate(180deg) saturate(100%);
}