.trip-container {
    position: relative;
    cursor: pointer;
    width: 320px;
    height: 240px;
}

.trip-image-container {
    width: 320px;
    height: 240px;
    object-fit: cover;
    filter: brightness(55%);
    border-radius: 10px;
}

.trip-destination {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 10px 10px 10px 10px;
    font-size: 18pt;
    font-weight: 700;
    color: var(--light-text-color);
}

.trip-duration {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 0;
    padding: 10px 10px 10px 10px;
    font-size: 14pt;
    font-weight: 400;
    color: var(--light-text-color);
}