.no-bullet {
    list-style-type: none;
    padding: 10px 0px 10px 0px;
    margin: 0px 0px 0px 0px;
}

.radio-li {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 25px;
    padding: 10px 0px 10px 5px;
    cursor: pointer;
    transition-duration: 200ms;
}

.radio-li:hover {
    background-color: var(--primary-highlight-color);
}

.radio-li:target {
    background-color: var(--primary-highlight-color);
}

.radio-icon-container {
    width: 35px;
    height: 35px;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}