.map-screen-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 100px;
    align-items: center;
    width: 100%;
    max-width: 1000px;
}

.map-screen-title-container {
    display: flex;
    width: 100%;
    max-width: 1000px;
}

.map-screen-title {
    font-size: 18pt;
    font-weight: 700;
}

.map-container {
    width: 100%;
    height: 600px;
    box-shadow: 2px 2px 4px 0px var(--main-shadow-color);
}

.marker-style {
    filter: hue-rotate(120deg);
}