.background-cover {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.05);
    backdrop-filter: blur(5px);
    z-index: 4;
}

.modal-container {
    display: flex;
    flex-direction: column;
    background-color: var(--list-background-color);
    border-radius: 5px;
    box-shadow: 0px 2px 4px 0px var(--main-shadow-color);
    width: fit-content;
    min-width: 400px;
    min-height: 300px;
    padding: 0px 0px 10px 0px;
    transition-duration: 200ms;
}

.constrained-width {
    width: 600px;
}

.modal-container.slidedown {
    animation: slidedown 200ms ease-out 0ms 1;
}

.modal-container.slideup {
    animation: none;
    transform: translateY(-200%);
}

.modal-content-container {
    padding: 10px 20px 10px 20px;
    flex-grow: 1;
}

.modal-title {
    font-size: 18pt;
    font-weight: 700;
}

.modal-text {
    font-size: 14pt;
    font-weight: 400;
    padding: 5px 0px 10px 0px;
    margin: 0;
}

.modal-button-container {
    display: flex;
    justify-content: flex-end;
    gap: 15px;
    padding: 10px 20px 10px 20px;
}

@keyframes slidedown {
    from {
        transform: translateY(-200%);
    }
    to {
        transform: translate(0%);
    }
}

@keyframes slideup {
    from {
        transform: translateY(0%);
    }
    to {
        transform: translate(-200%);
    }
}