.option-image-container {
    border-radius: 10px;
    width: 360px;
    height: 240px;
    object-fit: cover;
    filter: brightness(55%);
}

.option-relative-container {
    position: relative;
}

.option-info-container {
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    font-size: 18pt;
    font-weight: 600;
    padding: 5px 0px 0px 10px;
}

.ecopoints-container {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0px 10px 10px 0px;
}

.favorite-container {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0px 0px 10px 10px;
}

.favorite-icon {
    width: 30px;
    height: 30px;
    filter: invert(84%) sepia(31%) saturate(648%) hue-rotate(357deg) brightness(101%) contrast(90%);
    cursor: pointer;
}

.favorite-icon:hover {
    animation: bumpoutin 400ms ease-in-out 0ms 1;
}

@keyframes bumpoutin {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}