.popup-container {
    display: flex;
    flex-direction: column;
    /* width: 160px; */
}

.popup-title {
    font-size: 14pt;
    font-weight: 600;
    margin: 0;
    padding-bottom: 10px;
}

.popup-ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    font-size: 12pt;
    font-weight: 400;
}

.popup-li {
    margin: 0;
    padding: 0;
}

.popup-li ~ .popup-li {
    margin-top: 5px;
}