.perk-container {
    border-radius: 5px;
    width: 320px;
    box-shadow: 2px 2px 4px 0px var(--main-shadow-color);
    background-color: var(--navbar-color);
    cursor: pointer;
}

.perk-container:hover {
    animation: bumpoutin 400ms ease-in-out 0ms 1;
}

.perk-content-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    align-items: flex-start;
    color: var(--light-text-color);
}

.perk-id-container {
    display: flex;
    flex-direction: column;
    padding: 10px 10px 10px 10px;
}

.perk-title {
    font-size: 18pt;
    font-weight: 700;
    margin: 0;
}

.perk-p {
    margin: 0;
    font-size: 14pt;
    font-weight: 400;
}

.more-info-container {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.more-info-container-padding {
    padding: 10px;
}

.perk-icon-container {
    width: 50px;
    height: 50px;
    filter: invert(100%) sepia(1%) saturate(0%) hue-rotate(25deg) brightness(109%) contrast(100%);
}

@keyframes bumpoutin {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}