.achievement-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 150px;
    align-items: center;
}

.achievement-title {
    font-size: 18pt;
    font-weight: 700;
}

.achievement-text {
    font-size: 14pt;
    font-weight: 400;
    text-align: center;
}

.achievement-icon-container {
    padding: 10px;
}

.achievement-icon {
    width: 80px;
    height: 80px;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}