.card-description-container {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-bottom: 100px;
    gap: 20px;
}

.card-title {
    font-size: 20pt;
    font-weight: 700;
}

.card-description {
    font-size: 16pt;
    font-weight: 400;
}

.qr-container {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
}

.qr-card {
    width: 500px;
    height: auto;
}

.qr-scanner-container {
    display: flex;
    justify-content: center;
}

.qr-scanner-line {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.qr-icon {
    width: 400px;
    height: auto;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}

.qr-icon-container {
    padding: 10px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    cursor: pointer;
    max-width: fit-content;
}

.qr-icon-container:hover {
    background-color: var(--primary-highlight-color);
    animation: bumpoutin 400ms ease-in-out 0ms 1;
}

@keyframes bumpoutin {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}