.navbar-container {
    background-color: var(--navbar-color);
    display: flex;
    justify-content: center;
    width: 100%;
    box-shadow: 0px 2px 4px 0px var(--main-shadow-color);
    position: fixed;
    top: 0;
    z-index: 100;
}

.centering-container {
    background-color: var(--navbar-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
    padding: 5px 10px 5px 15px;
    gap: 20px;
}

.navbar-group-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
}

.user-container {
    cursor: pointer;
    padding: 10px;
    border-radius: 5px;
    transition-duration: 200ms;
}

.user-container:hover {
    background-color: var(--secondary-highlight-color);
}

.user-icon-container {
    width: 40px;
    height: 40px;
    filter: invert(100%) sepia(1%) saturate(0%) hue-rotate(25deg) brightness(109%) contrast(100%);
    
}

.username-p {
    color: var(--light-text-color);
    user-select: none;
    font-size: large;
}