.footer-container {
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: var(--list-background-color);
    z-index: 10;
}

.footer-centering {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
    width: 100%;
    max-width: 1000px;
    padding: 10px 0px 10px 0px;
}

.footer-item {
    padding: 10px 10px 10px 10px;
    cursor: pointer;
    transition-duration: 200ms;
    border-radius: 5px;
}

.footer-item:hover {
    background-color: var(--primary-highlight-color);
}

.footer-icon-container {
    width: 40px;
    height: 40px;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}