.list-container {
    display: flex;
    flex-direction: column;
    gap: 0px;
    padding: 10px 10px 20px 10px;
}

.list-header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-title-container {
    display: flex;
    gap: 10px;
    align-items: center;
}

.list-header-icon {
    width: 40px;
    height: 40px;
}

.list-container-title {
    font-weight: 700;
    font-size: x-large;
    color: black;
}

.sorting-icon-container {
    border-radius: 5px;
    padding: 5px 10px 5px 10px;
    cursor: pointer;
    transition-duration: 200ms;
}

.sorting-icon-container:hover {
    background-color: var(--primary-highlight-color);
}

.sorting-icon {
    width: 30px;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}

.options-list-container {
    display: flex;
    background-color: var(--list-background-color);
    border-radius: 10px;
    box-shadow: 2px 2px 4px var(--main-shadow-color);
    padding: 10px 10px 5px 10px;
    gap: 20px;
    max-width: 1000px;
    overflow-x: auto;
}