.schedule-container {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-bottom: 100px;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
}

.title-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1000px;
}

.schedule-container-left {
    display: flex;
    gap: 20px;
    align-items: center;
}

.schedule-title {
    font-size: 20pt;
    font-weight: 700;
}

.schedule-text {
    font-size: 14pt;
    font-weight: 400;
    margin: 0;
    padding: 0;
}

.steps-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-top: 40px;
    width: 100%;
    max-width: 1000px;
}

.step-container {
    width: 100%;
    max-width: 1000px;
}

.step-container.white {
    border-radius: 10px;
    box-shadow: 2px 2px 4px 0px var(--main-shadow-color);
    background-color: var(--list-background-color);
}

.step-container.blue {
    /* border-radius: 10px;
    box-shadow: 2px 2px 4px 0px var(--main-shadow-color); */
    background-color: var(--background-color);
}

.step-padding {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.step-icon-number-container {
    width: 60px;
    height: 60px;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}

.events-container {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    background-color: var(--main-highlight-color);
    padding: 10px;
    width: 400px;
    height: auto;
}