.main-container {
    background-color: var(--background-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.body-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 100px;
    width: 100%;
    max-width: 1000px;
}

.id-info-container {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 1000px;
    gap: 80px;
}

.id-icon-container {
    padding: 10px;
    cursor: pointer;
}

.id-icon {
    width: 300px;
    height: 300px;
    filter: invert(47%) sepia(95%) saturate(4389%) hue-rotate(179deg) brightness(89%) contrast(101%);
}

.id-info-text-container {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.id-li {
    margin: 0;
    padding: 0;
    font-size: 16pt;
    display: flex;
}

.id-li ~ .id-li {
    margin-top: 5px;
}

.id-attribute-name {
    font-weight: 700;
    width: 7em;
}

.id-attribute-content {
    font-weight: 400;
}

.reach-out-container {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    max-width: 1000px;
}

.reach-out-title-container {
    display: flex;
    align-items: center;
    gap: 20px;
}

.reach-out-title {
    font-size: 18pt;
    font-weight: 700;
}

.reach-out-icon {
    width: 40px;
    height: 40px;
}

.achievements-container {
    display: flex;
    justify-content: space-around;
}

.past-trips-container {
    display: flex;
    gap: 20px;
    overflow-x: auto;
    overflow-y: hidden;
    width: 100%;
    max-width: 1000px;
}

.prof-story-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.profile-story-container {
    width: 240px;
}